
.ms-iscroll {
  position: relative;
  background-color: #f5f5f5;
  height: 100%;
  overflow: hidden;
}
.ms-iscroll .ms-iscroll-bar {
  position: absolute;
  width: 4px;
  height: 3rem;
  border-radius: 2px;
  right: 0;
  top: 0;
  background-color: #000;
  opacity: 0;
  transition: all .5s;
  transition-timing-function: ease-out;
}
.ms-iscroll .ms-iscroll-bar.active {
  opacity: .4;
}
.ms-iscroll .ms-iscroll-content {
  overflow: hidden;
  transition-timing-function: ease-out;
}
.ms-iscroll .ms-pull-to-refresh {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}
.ms-iscroll .ms-pull-to-refresh.down .pull-to-refresh-arrow {
  transform: rotate(180deg);
}
.ms-iscroll .ms-pull-to-refresh.loading .pull-to-refresh-arrow {
  visibility: hidden;
  transition-duration: 0s;
}
.ms-iscroll .ms-pull-to-refresh.loading .preloader {
  visibility: visible;
}



